////
/// Fonts
/// @group base
////


/// @type Map
/// @prop {String} family - Font Family
/// @prop {String} name - Font Name
/// @prop {String} style - Font Style
/// @prop {Number} weight - Font Weight
/// @prop {String} filename - Font Filepath and Filename
$font-families-styles: (
    "Meta Sans": (
        ("Light",         normal, 300, "meta-sans/MetaWeb-Light"),
        ("Normal",        normal, 400, "meta-sans/MetaWeb-Normal"),
        ("Book",          normal, 500, "meta-sans/MetaWeb-Book"),
        ("Medium",        normal, 600, "meta-sans/MetaWeb-Medium"),
        ("Bold",          normal, 700, "meta-sans/MetaWeb-Bold"),
        ("Light Italic",  italic, 300, "meta-sans/MetaWeb-LightIta"),
        ("Normal Italic", italic, 400, "meta-sans/MetaWeb-NormalIta"),
        ("Book Italic",   italic, 500, "meta-sans/MetaWeb-BookIta"),
        ("Medium Italic", italic, 600, "meta-sans/MetaWeb-MediumIta"),
        ("Bold Italic",   italic, 700, "meta-sans/MetaWeb-BoldIta")
    ),
    "Roboto": (
        ("Thin",          normal, 100, "roboto/Roboto-100"),
        ("Light",         normal, 300, "roboto/Roboto-300"),
        ("Regular",       normal, 400, "roboto/Roboto-regular"),
        ("Medium",        normal, 600, "roboto/Roboto-600"),
        ("Bold",          normal, 700, "roboto/Roboto-700"),
        ("Black",         normal, 900, "roboto/Roboto-900"),
        ("Thin Italic",   italic, 100, "roboto/Roboto-100italic"),
        ("Light Italic",  italic, 300, "roboto/Roboto-300italic"),
        ("Italic",        italic, 400, "roboto/Roboto-italic"),
        ("Medium Italic", italic, 600, "roboto/Roboto-600italic"),
        ("Bold Italic",   italic, 700, "roboto/Roboto-700italic"),
        ("Black Italic",  italic, 900, "roboto/Roboto-900italic")
    ),
    "PT Sans": (
        ("Regular",     normal, 400, "pt-sans/PT-Sans-regular"),
        ("Bold",        normal, 700, "pt-sans/PT-Sans-700"),
        ("Italic",      italic, 400, "pt-sans/PT-Sans-italic"),
        ("Bold Italic", italic, 700, "pt-sans/PT-Sans-700italic")
    ),
    "Open Sans": (
        ("Light",            normal, 300, "open-sans/Open-Sans-300"),
        ("Regular",          normal, 400, "open-sans/Open-Sans-regular"),
        ("Semibold",         normal, 600, "open-sans/Open-Sans-600"),
        ("Bold",             normal, 700, "open-sans/Open-Sans-700"),
        ("Extrabold",        normal, 800, "open-sans/Open-Sans-800"),
        ("Light Italic",     italic, 300, "open-sans/Open-Sans-300italic"),
        ("Italic",           italic, 300, "open-sans/Open-Sans-italic"),
        ("Semibold Italic",  italic, 600, "open-sans/Open-Sans-600italic"),
        ("Bold Italic",      italic, 700, "open-sans/Open-Sans-700italic"),
        ("Extrabold Italic", italic, 800, "open-sans/Open-Sans-800italic")
    )
);
@each $family, $styles in $font-families-styles {
    @each $style in $styles {
        @font-face {
            font-family: $family;
            font-style: nth($style, 2);
            font-weight: nth($style, 3);
            src: local("#{$family} #{nth($style, 1)}"),
                 asset-url("/fonts/#{nth($style, 4)}.woff") format("woff");
            unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
        }
    }
}

$fa-font-path: "../fonts/fa4";
@import "../../node_modules/font-awesome/scss/font-awesome";
