////
/// Fallout
/// @group layout
////


.fallout {
    @extend .container;

    & > div {
        @include grid-column-map((
            "small": 8,
            "medium": 6
        ));
    }

    aside {
        @include grid-column(3, "medium");
    }
}

.fallout--none,
.fallout--both {
    aside {
        @include grid-column-map((
            "small": 4,
            "medium": 3
        ));
    }
}
.fallout--none,
.fallout--left {
    @include media("above:small") {
        @include fallout-figure-side(left);
    }

    & > div {
        @include grid-column-push-map((
            "small": 4,
            "medium": 3
        ));
    }
}

.fallout--left {
    @include media("above:small") {
        @include fallout-figure-side(left);
    }
}

.fallout--right {
    @include media("above:small") {
        @include fallout-figure-side(right);
    }
}

.fallout--both {
    @include media("above:medium") {
        @include fallout-figure-side(left);
        @include fallout-figure-side(right);
    }
}
