////
/// Hallway
/// @group patterns
////


.hallway {
    @include grid;

    li {
        @include grid-column(4);

        & + li {
            @include media("below:medium") {
                margin-top: $grid-padding;
            }
        }
    }

    a {
        // @include anchor(reset);
        @extend %anchor--reset;
        // @include anchor("hallway-image", ".hallway__image");
        @extend %anchor--hallway;
        background-color: $color-white;
        display: block;
        z-index: z-index(default);

        @include media("above:small", "below:medium") {
            display: flex;
            flex-direction: row;
        }

    }

    .hallway__image {
        width: 100%;
        display: block;

        @include media("above:small", "below:medium") {
            width: 40%;
            height: 100%;
            flex: 0 1 auto;
        }
    }

    .hallway__content {
        background-color: $color-white;
        padding: $measure-small-relative $measure-medium-relative;

        @include media("above:small", "below:medium") {
            padding-right: 0;
            padding-bottom: 0;
        }
    }

    .hallway__image + .hallway__content {
        margin: -$measure-medium-relative $measure-medium-relative 0;
        position: relative;

        @include media("above:small", "below:medium") {
            flex: 1;
            margin: 0;
        }
    }

    .hallway__content__title {
        width: 100%;
        display: block;
        padding-right: $measure-medium;
        margin-top: 0;

        &:after {
            @include font-family(icon);
            content: "\f105"; // fa-angle-right
            color: $color-black;
            position: absolute; // specificity
            top: 50%;
            right: 0;
            transform: translateY(-50%);
        }

        span {
            color: $color-black;
            border-bottom: $border-width-default solid transparent;
        }
    }
    a:hover .hallway__content__title,
    a:focus .hallway__content__title {
        &:after {
            color: $color-endeavour;
        }

        span {
            color: $color-endeavour;
            border-bottom-color: $color-endeavour;
        }
    }
    a:active .hallway__content__title {
        &:after {
            color: $color-endeavour;
        }

        span {
            color: $color-endeavour;
            border-bottom-color: $color-quill;
            border-bottom-style: dotted;
        }
    }

    .image-credit {
        top: -$grid-padding-half;

        @include media("above:small", "below:medium") {
            right: auto;
            left: calc(40% + #{$grid-padding-half});
            transform: translateX(-100%);
        }
    }
}
