////
/// Content
/// @group base
////


html {
    @include font-family(default);
    font-size: $base-font-size;
    font-style: normal;
    font-weight: $font-weight-default;
    line-height: $line-height-default;
}


/**
 * Content Elements
 */
p,
ul,
ol,
pre,
table,
blockquote,
figure {
    @extend %content-elements !optional;
    margin-bottom: $measure-medium;
 }


/**
 * Heading Elements
 */
h1,
h2,
h3,
h4,
h5,
h6 {
    @extend %heading-elements !optional;
    @include font-family(meta);
    width: 100%;
    display: block;
    flex: 1 0 auto;
    position: relative;
    margin-bottom: 1.5rem;
    font-weight: $font-weight-default;
}

.alpha,
%alpha {
    @include typographic-scale($font-size-alpha);
}
h1 {
    @extend %alpha !optional;
}

.beta,
%beta {
    @include typographic-scale($font-size-beta);
}
h2 {
    @extend %beta !optional;
}

.gamma,
%gamma {
    @include typographic-scale($font-size-gamma);
}
h3 {
    @extend %gamma !optional;
}

.delta,
%delta {
    @include typographic-scale($font-size-delta);
}
h4 {
    @extend %delta !optional;
}

.epsilon,
%epsilon {
    @include typographic-scale($font-size-default);
}
h5 {
    @extend %epsilon !optional;
}

.zeta,
%zeta {
    @include typographic-scale($font-size-default);
}
h6 {
    @extend %zeta !optional;
}


p {
    margin: 0 0 #{$line-height-default}em;

    &:last-child {
        margin-bottom: 0;
    }

    &:empty {
        display: none;
        visibility: hidden;
    }

    &.columns {
        @include media("above:large") {
            column-count: 2;
            column-gap: $measure-medium;
        }
    }
}


ul,
ol {
    list-style: none;

    & & {
        margin-top: 0;
        margin-bottom: 0;
    }
}


/**
 * Images and Figures
 */
img,
svg,
picture {
    @extend %image-elements !optional;
}
%image-elements {
    display: block;
    position: relative;
    overflow: hidden;

    &,
    a & {
        width: auto;
        max-width: 100%;
        height: auto;
    }
}
img {
    border-style: none;

    $image-broken-border-width: 4px;

    &::before {
        content: "";
        background-color: $color-gallery;
        background-image: linear-gradient(to bottom right,
                                          transparent 0%,
                                          transparent calc(50% - #{$image-broken-border-width / 2}),
                                          $color-quill calc(50% - #{$image-broken-border-width / 2}),
                                          $color-quill calc(50% + #{$image-broken-border-width / 2}),
                                          transparent calc(50% + #{$image-broken-border-width / 2}),
                                          transparent 100%
                          ),
                          linear-gradient(to bottom left,
                                          transparent 0%,
                                          transparent calc(50% - #{$image-broken-border-width / 2}),
                                          $color-quill calc(50% - #{$image-broken-border-width / 2}),
                                          $color-quill calc(50% + #{$image-broken-border-width / 2}),
                                          transparent calc(50% + #{$image-broken-border-width / 2}),
                                          transparent 100%
                          );
        width:  100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        box-shadow: inset 0 0 0 $image-broken-border-width $color-quill;
    }
}
svg:not(:root) {
    overflow: hidden;
}
figure {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 0 $measure-medium;

    & > a {
        display: block;
        border-bottom-width: 0;
    }

    & > a:link,
    & > a:visited,
    figcaption {
        color: $color-gallery;
        font-style: italic;
    }

    figcaption {
        @include font-family(serif);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: $measure-medium;
        line-height: $line-height-small;
    }
}


small {
    font-size: $font-size-small;
}

sub,
sup {
    position: relative;
    vertical-align: baseline;
    line-height: 0;
}
sup {
    top: -0.5em;
}
sub {
    bottom: -0.25em;
}
