////
/// Image Credit
/// @group patterns
////



.image-credit {
    color: $color-white;
    width: auto;
    max-width: 100%;
    min-height: 2.5em;
    padding: $measure-small;
    position: absolute;
    top:   0;
    right: 0;
    overflow: hidden;
    z-index: z-index(above);

    &[aria-expanded="true"] {
        background-color: $color-black;
    }

    .image-credit__button {
        background-color: transparent;
        color: $color-white;
        width:  2.25em;
        height: 2.25em;
        float: right;
        padding: 0;
        border: 0;
        position: relative;
        cursor: pointer;
        text-shadow: 0 0 0.5em rgba($color-black, 0.75);

        &:hover,
        &:focus,
        &:active {
            color: $color-yellow;
            outline: none;
            text-shadow: 0 0 0.5em $color-white;
        }

        // http://fontawesome.io/icon/info-circle/
        // http://fontawesome.io/icon/info
        .fa-info-circle,
        .fa-info {
            @include font-family(icon);
            @include transition;
            color: inherit;
            font-size: 1.357em; // 19px
        }
        &[aria-pressed="false"] {
            .fa-info-circle {
                display: block;
            }

            .fa-info {
                display: none;
            }
        }
        &[aria-pressed="true"] {
            .fa-info-circle {
                display: none;
            }

            .fa-info {
                display: block;
            }
        }
    }

    .image-credit__text {
        display: table-cell;
        padding: 0.588em; // = 0.5rem / 0.85em
        font-size: 0.85em;
        line-height: 1.5;

        a {
            color: $color-white;
            text-decoration: none;
            border-bottom: 1px dotted $color-quill;

            &:hover,
            &:focus {
                border-bottom-color: $color-white;
            }

            &:active {
                border-bottom-color: transparent;
            }
        }
    }
    .image-credit__button[aria-pressed="false"] {
        & + .image-credit__text {
            @include visually-hidden;
        }
    }
}
.image-credit--bottom-right {
    bottom: 0;
    top: auto;
}
.image-credit--top-left {
    left: 0;
    right: auto;

    .image-credit__button {
        float: left;
    }
}
