.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto;
}

html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

body {
    margin: 0;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
ol,
ul {
    margin: 0;
    padding: 0;
}

main,
li {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
}

strong {
    font-weight: 700;
}

a,
button {
    color: inherit;
    transition: all 0.2s ease;
}

a {
    text-decoration: none;
}

button {
    overflow: visible;
    border: 0;
    font: inherit;
    -webkit-font-smoothing: inherit;
    letter-spacing: inherit;
    background: none;
    cursor: pointer;
}

::-moz-focus-inner {
    padding: 0;
    border: 0;
}

:focus {
    outline: 0;
}

img {
    max-width: 100%;
    height: auto;
    border: 0;
}

html {
    min-height: 100%;
    position: relative;
    overflow-y: scroll;
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    background-color: #ffffff;
    color: #1e1e1e;
}

html {
    font-family: "PT Sans", Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
}

/**
 * Content Elements
 */
p,
ul,
ol,
pre,
table,
blockquote,
figure {
    margin-bottom: 1rem;
}

/**
 * Heading Elements
 */
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Meta Sans", Arial, Helvetica, sans-serif;
    font-display: swap;
    width: 100%;
    display: block;
    flex: 1 0 auto;
    position: relative;
    margin-bottom: 1.5rem;
    font-weight: 400;
}

.alpha,
h1 {
    font-size: 50.5679px;
    font-size: 3.16049rem;
    line-height: 1.424;
}

.beta,
h2 {
    font-size: 37.92593px;
    font-size: 2.37037rem;
    line-height: 1.266;
}

.gamma,
h3 {
    font-size: 28.44444px;
    font-size: 1.77778rem;
    line-height: 1.688;
}

.delta,
h4 {
    font-size: 21.33333px;
    font-size: 1.33333rem;
    line-height: 1.125;
}

.epsilon,
h5 {
    font-size: 16px;
    font-size: 1rem;
    line-height: 1.5;
}

.zeta,
h6 {
    font-size: 16px;
    font-size: 1rem;
    line-height: 1.5;
}

p {
    margin: 0 0 1.5em;
}

p:last-child {
    margin-bottom: 0;
}

p:empty {
    display: none;
    visibility: hidden;
}

@media (min-width: 1201px) {
    p.columns {
        column-count: 2;
        column-gap: 1rem;
    }
}

ul,
ol {
    list-style: none;
}

ul ul,
ol ul, ul
ol,
ol
ol {
    margin-top: 0;
    margin-bottom: 0;
}

/**
 * Images and Figures
 */
img,
svg,
picture {
    display: block;
    position: relative;
    overflow: hidden;
}

img,
svg,
