@media (max-width: 500px) {
    .primary-navigation {
        padding-left: 0.5rem;
        padding-right: 0.5rem;
    }
}

@media (min-width: 501px) {
    .primary-navigation {
        padding-left: 1rem;
        padding-right: 1rem;
    }
}

.primary-navigation__list {
    font-size: 0;
}

.primary-navigation__list > li {
    display: inline-block;
    position: relative;
    font-size: 1rem;
}

.primary-navigation__list > li a {
    display: block;
}

.primary-navigation__list > li.has-menu > a:after {
    font-family: "FontAwesome";
    font-display: swap;
    content: "\2003\f0d7";
    font-size: 1.33333rem;
}

.primary-navigation__list > li .primary-navigation__sub-list {
    background-color: #ffffff;
    opacity: 1;
    min-width: 16em;
    max-width: 500px;
    border: 4px solid #4c2c92;
    border-radius: 0 0.25rem 0.25rem 0.25rem;
    visibility: visible;
    position: absolute;
    top: 100%;
    left: 0;
    transform: translateY(-2px);
    transition: opacity 0.2s ease;
    z-index: 2;
}

.primary-navigation__list > li .primary-navigation__sub-list a {
    width: 100%;
    padding: 1rem;
}

.primary-navigation__list > li:not(:hover):not(:active) .primary-navigation__sub-list {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.4s ease, visibility 0s ease 0.4s;
}

.hallway {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    width: calc(100% + 1rem);
    float: left;
    margin-left: -0.5rem;
    margin-right: -0.5rem;
}

@supports (display: flex) {
    .hallway {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}

@supports (display: flex) {
    .hallway {
        float: none;
    }
}

.hallway li {
    min-height: 1px;
    float: left;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    position: relative;
}

@supports (display: flex) {
    .hallway li {
        float: none;
        flex: 0 1 auto;
    }
}

@media (max-width: 800px) {
    .hallway li {
        width: 100%;
    }
}

@media (min-width: 801px) {
    .hallway li {
        width: 33.333%;
    }
}

@media (max-width: 800px) {
    .hallway li + li {
        margin-top: 1rem;
    }
}

.hallway a {
    background-color: #ffffff;
    display: block;
    z-index: 1;
}

@media (min-width: 501px) and (max-width: 800px) {
    .hallway a {
        display: flex;
        flex-direction: row;
    }
}

.hallway .hallway__image {
    width: 100%;
    display: block;
}

@media (min-width: 501px) and (max-width: 800px) {
    .hallway .hallway__image {
        width: 40%;
        height: 100%;
        flex: 0 1 auto;
    }
}

.hallway .hallway__content {
    background-color: #ffffff;
    padding: 0.5em 1em;
}

@media (min-width: 501px) and (max-width: 800px) {
    .hallway .hallway__content {
        padding-right: 0;
        padding-bottom: 0;
    }
}

.hallway .hallway__image + .hallway__content {
    margin: -1em 1em 0;
    position: relative;
}

@media (min-width: 501px) and (max-width: 800px) {
    .hallway .hallway__image + .hallway__content {
        flex: 1;
        margin: 0;
    }
}

.hallway .hallway__content__title {
    width: 100%;
    display: block;
    padding-right: 1rem;
    margin-top: 0;
}

.hallway .hallway__content__title:after {
    font-family: "FontAwesome";
    font-display: swap;
    content: "\f105";
    color: #1e1e1e;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
}

.hallway .hallway__content__title span {
    color: #1e1e1e;
    border-bottom: 2px solid transparent;
}

.hallway a:hover .hallway__content__title:after,
.hallway a:focus .hallway__content__title:after {
    color: #005ea5;
}

.hallway a:hover .hallway__content__title span,
.hallway a:focus .hallway__content__title span {
    color: #005ea5;
    border-bottom-color: #005ea5;
}

.hallway a:active .hallway__content__title:after {
    color: #005ea5;
}

.hallway a:active .hallway__content__title span {
    color: #005ea5;
    border-bottom-color: #d0cfcb;
    border-bottom-style: dotted;
}

.hallway .image-credit {
    top: -0.5rem;
}

@media (min-width: 501px) and (max-width: 800px) {
    .hallway .image-credit {
        right: auto;
        left: calc(40% + 0.5rem);
        transform: translateX(-100%);
    }
}

.widget {
    background-color: #ececec;
    padding: 1rem;
}

.widget__list li {
    display: block;
}

.widget__list a {
    display: block;
}

.widget__list a::before {
    font-family: "FontAwesome";
    font-display: swap;
    content: "\f105";
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.image-credit {
    color: #ffffff;
    width: auto;
    max-width: 100%;
    min-height: 2.5em;
    padding: 0.5rem;
    position: absolute;
    top: 0;
    right: 0;
    overflow: hidden;
    z-index: 2;
}

.image-credit[aria-expanded="true"] {
    background-color: #1e1e1e;
}

.image-credit .image-credit__button {
    background-color: transparent;
    color: #ffffff;
    width: 2.25em;
    height: 2.25em;
    float: right;
    padding: 0;
    border: 0;
    position: relative;
    cursor: pointer;
    text-shadow: 0 0 0.5em rgba(30, 30, 30, 0.75);
}

.image-credit .image-credit__button:hover, .image-credit .image-credit__button:focus, .image-credit .image-credit__button:active {
    color: #fbae3b;
    outline: none;
    text-shadow: 0 0 0.5em #ffffff;
}

.image-credit .image-credit__button .fa-info-circle,
.image-credit .image-credit__button .fa-info {
    font-family: "FontAwesome";
    font-display: swap;
    transition: all 0.2s ease;
    color: inherit;
    font-size: 1.357em;
}

.image-credit .image-credit__button[aria-pressed="false"] .fa-info-circle {
    display: block;
}

.image-credit .image-credit__button[aria-pressed="false"] .fa-info {
    display: none;
}

.image-credit .image-credit__button[aria-pressed="true"] .fa-info-circle {
    display: none;
}

.image-credit .image-credit__button[aria-pressed="true"] .fa-info {
    display: block;
}

.image-credit .image-credit__text {
    display: table-cell;
    padding: 0.588em;
    font-size: 0.85em;
    line-height: 1.5;
}

.image-credit .image-credit__text a {
    color: #ffffff;
    text-decoration: none;
    border-bottom: 1px dotted #d0cfcb;
}

.image-credit .image-credit__text a:hover, .image-credit .image-credit__text a:focus {
    border-bottom-color: #ffffff;
}

.image-credit .image-credit__text a:active {
    border-bottom-color: transparent;
}

.image-credit .image-credit__button[aria-pressed="false"] + .image-credit__text {
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    border: 0 !important;
    position: absolute !important;
    overflow: hidden !important;
    clip: rect(1px, 1px, 1px, 1px) !important;
}

.image-credit--bottom-right {
    bottom: 0;
    top: auto;
}

.image-credit--top-left {
    left: 0;
    right: auto;
}

.image-credit--top-left .image-credit__button {
    float: left;
}

.tags-list {
    padding-left: 2rem;
    position: relative;
}

.tags-list::before {
    font-family: "FontAwesome";
    font-display: swap;
    content: "\f0f7";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    font-size: 1.33333rem;
}

.tags-list li + li {
    margin-top: 0.5rem;
}

.lazy-load {
    transition: opacity 0.2s ease 0;
    opacity: 0;
    width: 100%;
    height: 0;
    display: block;
}

.lazy-load.loaded {
    opacity: 1;
    height: auto;
}

