////
/// Media
/// @group patterns
////


$ratios: (
    (16, 10): 62.50%,
    (16, 9):  56.25%,
    (4, 3):   75.00%
);

@each $ratio, $percentage in $ratios {
    .img--#{nth($ratio, 1)}-#{nth($ratio, 2)} {
        height: $percentage;
    }
    .media--#{nth($ratio, 1)}-#{nth($ratio, 2)} {
        padding-top: $percentage;
    }
}

.media--vimeo,
.media--youtube {
    @extend .media--16-9;
}
