////
/// Variables
/// @group abstracts
////


/// @type Length
$base-font-size: 16px !default;


/// @type Length
$grid-width-small:  500px !default;
/// @type Length
$grid-width-medium: 800px !default;
/// @type Length
$grid-width-large: 1200px !default;
/// @type Map
/// @prop {String} key - Breakpoint name
/// @prop {Length} value - Breakpoint value
/// @require {Variable} $grid-width-small
/// @require {Variable} $grid-width-medium
/// @require {Variable} $grid-width-large
$breakpoints: (
    "small":  $grid-width-small,
    "medium": $grid-width-medium,
    "large":  $grid-width-large
) !default;


/// @type Number
$grid-columns: 12 !default;
/// @type Length
$grid-padding: 1rem !default;
/// @type Length
/// @require {Variable} $grid-padding
$grid-padding-half: ($grid-padding / 2) !default;
/// @type Length
$fixed-sidebar-width: 17.5rem !default;
// 21em
/// @type Length
$line-length-minimum: #{$base-font-size * 21} !default;
// 30em
/// @type Length
$line-length-maximum: #{$base-font-size * 30} !default;
/// @type Length
$line-length-responsive: calc(#{$line-length-minimum} + (#{strip-units($line-length-maximum)} - #{strip-units($line-length-minimum)}) * ((100vw - #{$grid-width-small}) / (#{strip-units($grid-width-large)} - #{strip-units($grid-width-small)})));


/// @type Length
$measure-small:           0.500rem !default;
/// @type Length
$measure-small-relative:  0.500em !default;
/// @type Length
$measure-small-proportional:  2vmin !default;
/// @type Length
$measure-medium:          1.000rem !default;
/// @type Length
$measure-medium-relative: 1.000em !default;
/// @type Length
$measure-medium-proportional: 4vmin !default;
/// @type Length
$measure-large:           2.000rem !default;
/// @type Length
$measure-large-relative:  2.000em !default;
/// @type Length
$measure-large-proportional:  8vmin !default;


/// @type Number
$opacity-tint: 0.100 !default;
/// @type Number
$opacity-half: 0.500 !default;
/// @type Number
$opacity-shade: 0.850 !default;
/// @type Map
/// @prop {String} key - Opacity name
/// @prop {Number} value - Opacity value
/// @see {Function} opacities
$opacities: (
    "tint":  $opacity-tint,
    "half":  $opacity-half,
    "shade": $opacity-shade
) !default;


/// @type Color
$transparent: rgba(0, 0, 0, 0);
/// @type Color
$color-white: #ffffff !default;
/// @type Color
$color-gallery: #ececec !default;
/// @type Color
$color-quill: #d0cfcb !default;
/// @type Color
$color-black: #1e1e1e !default;

/// @type Color
$color-endeavour: #005ea5 !default;
/// @type Color
/// @require {Variable} $color-endeavour
$color-blue: $color-endeavour !default;
/// @type Color
$color-powder: mix($color-white, $color-endeavour, 50%);
/// @type Color
$color-curious: #2b8cc4 !default;
/// @type Color
$color-daisy: #4c2c92 !default;

/// @type Color
$color-alizarin: #d92231 !default;
/// @type Color
/// @require {Variable} $color-alizarin
$color-red: $color-alizarin !default;
/// @type Color
$color-flamingo: #f15f22 !default;
/// @type Color
/// @require {Variable} $color-flamingo
$color-orange: $color-flamingo !default;
/// @type Color
$color-yellow: #fbae3b !default;
/// @type Color
/// @require {Variable} $color-yellow
$color-yellow-orange: $color-yellow !default;
/// @type Color
$color-sherpa: #00444d !default;
/// @type Color
$color-stromboli: #39655d !default;
/// @type Color
$color-neptune: #86c5b3 !default;
/// @type Color
$color-montecarlo: #88d1ce !default;
/// @type Color
/// @require {Variable} $color-montecarlo
$color-monte-carlo: $color-montecarlo !default;
/// @type Color
$color-prussian: #00284d !default;
/// @type Color
/// @require {Variable} $color-prussian
$color-prussian-blue: $color-prussian !default;
/// @type Color
$color-allports: #0077a0 !default;
/// @type Color
$color-picton: #49c7ed !default;
/// @type Color
$color-vivid: #842b91 !default;
/// @type Color
/// @require {Variable} $color-vivid
$color-vivid-violet: $color-vivid !default;
/// @type Color
$color-violet: #ee3780 !default;
/// @type Color
/// @require {Variable} $color-violet
$color-violet-red: $color-violet !default;


/// @type Length
$border-width-default:  2px !default;
/// @type Length
$border-width-hairline: 1px !default;
/// @type Length
$border-width-thick: 4px !default;


/// @type String
$border-style-default: solid !default;


/// @type Length
/// @see {Mixin} border-radius
$border-radius-default: 0.250rem !default;
/// @type Length
/// @see {Mixin} border-radius
$border-radius-thin:    0.125rem !default;


/// @type Number
$font-weight-light:   300 !default;
/// @type Number
$font-weight-default: 400 !default;
/// @type Number
$font-weight-book:    500 !default;
/// @type Number
$font-weight-medium:  600 !default;
/// @type Number
$font-weight-bold:    700 !default;

/// @type Number
$font-size-ratio: 4/3;

// 16px
/// @type Length
/// @require {Variable} $base-font-size
$font-size-default: 1rem !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-default
$font-size-delta: ($font-size-ratio * $font-size-default) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-delta
$font-size-gamma: ($font-size-ratio * $font-size-delta) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-gamma
$font-size-beta: ($font-size-ratio * $font-size-gamma) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-beta
$font-size-alpha: ($font-size-ratio * $font-size-beta) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-default
$font-size-small: ($font-size-default / $font-size-ratio) !default;

/// @type List
$font-family-default: "PT Sans", Arial, Helvetica, sans-serif !default;
/// @type List
$font-family-meta: "Meta Sans", Arial, Helvetica, sans-serif !default;
/// @type List
$font-family-serif: Georgia, "Times New Roman", Times, serif !default;
/// @type List
$font-family-monospace: "Courier New", Courier, monospace !default;
/// @type List
$font-family-icon: "FontAwesome" !default;
/// @type Map
/// @prop {String} key - Font Family name
/// @prop {Number} value - Font Family value
/// @require {Variable} $font-family-default
/// @require {Variable} $font-family-meta
/// @require {Variable} $font-family-serif
/// @require {Variable} $font-family-monospace
/// @see {Mixin} font-family
$font-families: (
    "default":   $font-family-default,
    "meta" :     $font-family-meta,
    "serif":     $font-family-serif,
    "monospace": $font-family-monospace,
    "icon":      $font-family-icon
) !default;


// 2:3 - perfect fifth
/// @type Number
$line-height-default: 1.500 !default;
// 1:2  aug. fourth / dim. fifth
/// @type Number
$line-height-augmented-fourth: 1.414 !default;
/// @type Number
/// @require {Variable} $line-height-aug-fourth
$line-height-diminished-fifth: $line-height-augmented-fourth !default;
/// @type Number
$line-height-small: 1.200 !default;


/// @type Length
$rotation-default: 10deg !default;


/// @type Length
/// @see {Mixin} transition
$transition-time: 0.200s !default;
/// @type String
/// @see {Mixin} transition
$transition-function: ease !default;


/// @type Map
/// @prop {String} key - z-index name
/// @prop {Number} value - z-index value
/// @see {Function} z-index
$z-indexes: (
    "below":       -1,
    "root":         0,
    "default":      1,
    "above":        2,
    "modal":        3,
    "dialog":       4,
    "notification": 5
) !default;
