////
/// Primary Navigation
/// @group patterns
////


.primary-navigation {
    @include grid-top;
}


.primary-navigation__list {
    font-size: 0;

    // Top-level Links
    & > li {
        display: inline-block;
        position: relative;
        font-size: 1rem;

        a {
            display: block;
        }

        // Top-level Link with a sub-menu
        &.has-menu > a {
            &:after {
                @include font-family(icon);
                // EM-SPACE, Font Awesome `caret-down`
                content: "\2003\f0d7";
                font-size: $font-size-delta;
            }
        }

        // Selected Top-level Link
        &.is-selected > a {

        }

        // Sub-Menu
        .primary-navigation__sub-list {
            background-color: $color-white;
            opacity: 1;
            min-width: 16em;
            max-width: $grid-width-small;
            border: $border-width-thick $border-style-default $color-daisy;
            border-radius: 0 $border-radius-default $border-radius-default $border-radius-default;
            visibility: visible;
            position: absolute;
            top: 100%;
            left: 0;
            transform: translateY(-$border-width-default);
            transition: opacity $transition-time $transition-function;
            z-index: z-index(above);

            a {
                width: 100%;
                padding: $measure-medium;
            }
        }

        &:not(:hover):not(:active) {
            .primary-navigation__sub-list {
                opacity: 0;
                visibility: hidden;
                transition: opacity ($transition-time * 2) $transition-function,
                            visibility 0s $transition-function ($transition-time * 2);
            }
        }
    }
}
