////
/// Widgets
/// @group patterns
////


.widget {
    background-color: $color-gallery;
    padding: $measure-medium;
}


.widget__list {
    li {
        display: block;
    }

    a {
        display: block;

        &::before {
            @include font-family(icon);
            content: "\f105";
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
        }
    }
}
