////
/// Accordion
/// @group patterns
////


.accordion {

}

.js {
    .accordion__heading.inactive + div {
        @include visually-hidden;
    }
}

.accordion__heading__icon {
    @extend .fa;

    .accordion__heading.inactive > a > & {
        @extend .fa-plus;
    }

    .accordion__heading.active > a > & {
        @extend .fa-minus;
    }
}
