////
/// Tags List
/// @group patterns
////


.tags-list {
    padding-left: $measure-large;
    position: relative;

    &::before {
        @include font-family(icon);
        content: "\f0f7";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        font-size: $font-size-delta;
    }

    li + li {
        margin-top: $measure-small;
    }
}
